.ChangeAlert-bg{
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:#1e1e1f50;
    z-index:2
}

.ChangeAlert-container{
    position:fixed;
    bottom:-10px;
    left:-10px;
    right:-10px;
    padding:34px;
    background-color:#1e1e1f;
    color:#f2f4f5;
    z-index:2;
    text-align:center;
    font-size:20px
}

.ChangeAlert-container button{
    margin-top:16px;
    margin-bottom:16px;
    margin-right: auto;
    margin-left: auto;
}


.SyncTodosButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    transform: rotate(0);
    transition: .5s ease;
    animation: 2s infinite changeAnimation;
}

.SyncTodosButton:hover {
    transform: rotate(360deg);
    animation: none;
}

@keyframes changeAnimation {
    25% {
        box-shadow: 0px 5px 25px 5px rgba(97, 218, 250, 0.5);
        background-color: #59c9e7;
    }
    37.5% {
        box-shadow: 0px 5px 25px 10px rgba(97, 218, 250, 0.5);
        background-color: #44b7d6;
    }
    50% {
        box-shadow: 0px 5px 25px 15px rgba(97, 218, 250, 0.5);
        background-color: #44b7d6;
    }
    62.5% {
        box-shadow: 0px 5px 25px 10px rgba(97, 218, 250, 0.5);
        background-color: #44b7d6;
    }
    75% {
        box-shadow: 0px 5px 25px 5px rgba(97, 218, 250, 0.5);
        background-color: #59c9e7;

    }
    100% {
        box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
        background-color: #61DAFA;

    }
}